		},
		requestmenu:function(){
			
			uni.request({
				url:this.url+"Logins/logins?id="+this.$store.state.jiaoseID,
				method:"GET",
				success:(res)=> {
					console.log(res)
					this.list=res.data;
					
				}
			})
		},
